  background-size: contain;
  background-position: center;
  border: 1px solid;
  position: absolute;
  border-radius: 10px;
  z-index: 2;
}

.active {
  border: 5px solid yellow
}

.active:hover {
  cursor: pointer;
}

.player1Deck{
  bottom: 10%;
